ons-alefalefalef.woff') format(\"woff\");\n}\n\n.ivrita-switch *, .ivrita-mode-changer * {\n  margin: 0;\n  padding: 0;\n  outline: 0;\n  font-size: 30px;\n  font-weight: normal;\n  font-style: normal;\n  border: 0;\n  text-decoration: none;\n  list-style-type: none;\n  min-width: auto;\n  min-height: auto;\n  max-width: none;\n  max-height: none;\n  -webkit-text-stroke: none;\n  -moz-text-stroke: initial;\n  -ms-text-stroke: initial;\n  word-spacing: normal;\n  text-align: left;\n  width: auto;\n  height: auto;\n  position: static;\n  display: inline-block;\n  border: 0;\n  float: none;\n  background: none;\n  border-radius: 0;\n  box-shadow: none;\n  direction: ltr;\n  visibility: visible;\n  opacity: 1;\n  text-shadow: none;\n  outline: 0;\n  vertical-align: unset;\n  white-space: normal;\n  letter-spacing: 0;\n}\n\n.ivrita-switch {\n  z-index: 999999;\n  background-color: rgba(255, 255, 255, 0.8);\n  position: fixed;\n  top: calc(50% - 1em);\n  left: -1px;\n  border-radius: 0 7px 7px 0;\n  transition: .2s all ease-out .1s;\n  border: 1px solid #222;\n  font-size: 30px;\n  min-width: 40px;\n  padding: 3px 0 0;\n  overflow: hidden;\n  \n  &:hover {\n    top: calc(50% - 66px);\n  }\n\n  *, &:before, &:after {\n    box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n  }\n  \n  &.ivrita-switch--right {\n    left: auto;\n    right: -1px;\n    border-radius: 7px 0 0 7px;\n  }\n\n  a {\n    font-family: 'Ivritacons' !important;\n    font-weight: normal !important;\n    font-style: normal !important;\n    transition: .1s all ease-out;\n    display: block;\n    color: #2d2828;\n    line-height: 1em;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n    text-align: center;\n    width: 33px;\n    height: 33px;\n    line-height: 33px;\n    margin: 0 3px;\n    overflow: hidden;\n\n    &.ivrita-logo {\n      transition: .1s all ease-out;\n    }\n  }\n\n  &:hover a.ivrita-logo {\n    height: 0;\n    v